import React from 'react'
import { TabBar } from 'antd-mobile';

class Sp extends React.Component{
// const Sp = (props) => {
    constructor(props){
        super(props);
        this.state ={
            selectedTab:'redTab',
        };
    }
    render(props){
        const{pathname}=this.props.location;
    return (
        <div>
            <button onClick={() =>
                this.props.history.goBack()
            }
            style={{
                marginTop:'30px',
                // position:'fixed'
            }}>{`<<=`}</button>
            <img 
            style={{
                width:'230px',
                margin:'70px',
                position:'fixed'  
            }}
            src={this.props.location.data.img}></img>
            <p>{this.props.location.data.title}</p>
            <p>{this.props.location.data.price}</p>
            <div style={{
                position: 'fixed',
                width: '100%',
                bottom: 0
            }}>
                <div style={{float:'left',
                    width:'50%'}}>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="店铺"
                        icon={
                            <i className='iconfont icon-dianpu'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-dianpu'></i>
                        }
                        onPress={() => {
                            props.history.push('/cart');
                        }}
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        title="客服"
                        icon={
                            <i className='iconfont icon-kefu'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-kefu'></i>
                        }
                        onPress={() => {
                            props.history.push('/list');
                        }}
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        title="收藏"
                        icon={
                            <i className='iconfont icon-shoucang'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-shoucang1'></i>
                        }
                        selected={this.state.selectedTab=='redTab'}
                       onPress={()=>{
                        
                           this.setState({
                               selectedTab:'redTab'
                           })
                               
                           }
                       }
                        data-seed="logId"
                    >

                    </TabBar.Item>
                    
                </TabBar></div>
                <div style={{width:'50%',float:"right",
                            backgroundColor:'rgb(247, 163, 38)'}}>
                    <TabBar>
                    <TabBar.Item
                        title="加入购物车"
                        icon={
                            <i className='iconfont icon-4sdian'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-4sdian'></i>
                        }
                        onPress={() => {
                            props.history.push('/cart');
                        }}
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        title="立即购买"
                        icon={
                            <i className='iconfont icon-4sdian'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-4sdian'></i>
                        }
                        onPress={() => {
                            props.history.push('/list');
                        }}
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    </TabBar>
                </div>
            </div>
        </div>
    )
}
}

export default Sp
